export default {
	props: {
		type:{ // 是否是input类型
			type: String,
			default: 'input'
		},
		inputType:{ // input的类型
			type: String,
			default: 'text'
		},
		label: {
			type: String,
			default: ''
		},
		content: {
			type: String || Number,
			default: ''
		},
		required: {
			type: Boolean,
			default: false
		},
		disabled: {
			type: Boolean,
			default: false
		},
		scaleInput:{
			type: Boolean,
			default: false
		},
		placeholder: {
			type: String,
			default: ''
		},
		isSell: {
			type: Boolean,
			default: false
		},
		inputFloat:{
			type: String,
			default: 'right'
		},
		maxLength:{
			type: Number || String,
			default: 140
		},
		hasSlot: {
			type: Boolean,
			default: false
		},
		// 用于城市选择器
		withPlugin: {
			type: Boolean,
			default: false
		},
		radioValue: {
			type: Number,
			default: 1
		}
	},
	data(){
		return {
			// value: ''
		}
	},
	computed: {
		value(){
			return this.content
		},
		currentRadio(){
			return this.radioValue
		}
	},
	methods: {
		handleInput(e){
			this.$emit('on-input',e.detail.value)
		},
		handleClick(e){
			this.$emit('on-click')
		},
		handleBlur(){
			this.$emit('on-blur')
		},
		radioChange(e){
			this.$emit('on-radio-change',Number(e.detail.value))
		}
	}
}
